import password from '../img/password.png'
import persona from '../img/persona.png'
import dinero from '../img/money.png'

function Card4(){
    return (
        <div className='bg_white container_paso_giro_directo border_cards'>
            <p className='h3 mb-4 mb-lg-5'><strong>Desde nuestras oficinas</strong></p>
            
            <div className='d-flex align-items-center mb-4'>
                <img src={password} alt="cono de celular" />
                <p className='black mb-0 ps-3'>Conocer el <strong>cdigo MTCN </strong>&#40;Cdigo del Giro&#41;.</p>
            </div>

            <div className='d-flex align-items-center mb-4'>
                <img src={persona} alt="cono de persona" />
                <p className='black mb-0 ps-3'><strong>Nombre del remitente </strong>&#40;persona que te<br/>
                    enva el Giro&#41;.</p>
            </div>

            <div className='d-flex align-items-center mb-4'>
                <img src={dinero} alt="cono de persona" />
                <p className='black mb-0 ps-3'><strong>Pas desde donde te envan </strong>y el <strong>valor<br/>
                    del Giro</strong>.</p>
            </div>
        </div>

    )
}


export default Card4;